//Ermöglicht verschiedene Optionen, nach Bildern zu suchen (Unsplash/URL)

import React, { Component } from "react";
import Popup from "reactjs-popup";
import UnsplashSearch from "./UnsplashSearch";

class ImageWrapper extends Component {
  state = {
    alt: this.props.alt,
    imageURL: "",
    unsplashKeyword: ""
  };

  //Anzeige der Suche nach den Wörtern des input-Feldes in UnsplashSearch
  imageSearch() {
    if (this.state.unsplashKeyword) {
      return (
        <UnsplashSearch
          keyword={this.state.unsplashKeyword}
          setImage={image => {
            //image: URL des ausgewöhlten Bildes
            this.setState({
              image,
              unsplashKeyword: null
            });
            this.props.updateImage(image);
          }}
        />
      );
    } else {
      return <div>Type keyword to search image...</div>;
    }
  }
  render() {
    //Falls bereits ein Bild gespeichert wurde, zeige dieses an. Bei klick auf Papierkorb wird das Bild wieder gelöscht
    if (this.props.image) {
      return (
        <div className="exercise-image">
          <img
            className="ui rounded image exercise-image"
            src={this.props.image}
            alt={"Image of " + this.state.alt}
            width="20%"
          />
          <div className="overlay">
            <i
              className="huge trash icon"
              onClick={() => this.props.updateImage("")}
            ></i>
          </div>
        </div>
      );
    } else {
      //Es werden zwei Möglichkeiten, ein Bild hinzuzufügen, angeboten:
      //1. Gebe die URL eines Bildes an (Bestätigung durch ENTER)
      //2. Suche auf unsplash.com nach einem Bild.
      //  Sobald das Inputfeld angeklickt wird, können Ergebnisse in einem Popup (siehe Popup.jsx) gesehen werden
      return (
        <div>
          <div className="ui placeholder segment">
            <div className="ui two column very relaxed stackable grid">
              <div className="column">
                <div className="field">
                  <label htmlFor="urlInput">Provide image's URL:</label>
                  <input
                    id="urlInput"
                    value={this.state.imageURL}
                    onChange={event =>
                      this.setState({ imageURL: event.target.value })
                    }
                    onKeyDown={e => {
                      if (e.key === "Enter") {
                        this.props.updateImage(this.state.imageURL);
                        this.setState({
                          image: this.state.imageURL,
                          imageURL: ""
                        });
                      }
                    }}
                  ></input>
                </div>
              </div>
              <div className="middle aligned column">
                <div className="field">
                  <label htmlFor="unsplashInput">Search in unsplash.com:</label>
                  <Popup
                    width="500px"
                    contentStyle={{ width: "430px" }}
                    trigger={
                      <input
                        id="unsplashInput"
                        onChange={event =>
                          this.setState({ unsplashKeyword: event.target.value })
                        }
                        autoComplete="off"
                      ></input>
                    }
                    on="focus"
                  >
                    {this.imageSearch()}
                  </Popup>
                </div>
              </div>
            </div>
            <div className="ui vertical divider">Or</div>
          </div>
        </div>
      );
    }
  }
}

export default ImageWrapper;
